$primary_color: #007cba;
$secondary_color: #5f5f5f;
$background_color: #dcdcde;
$white_color: #ffffff;
$border_color: #dddddd;

/** Start Admin Panel scss **/
.post-grid-block-inspector-control {
    .post-grid-block-tab {
        background-color: $background_color;
        border: none;
        border-radius: 0;
        box-shadow: none;
        cursor: pointer;
        font-weight: 500;
        height: 60px;
        padding: 3px 25px;

        &.active-tab {
            background: $white_color;
            border-top: 2px solid $primary_color !important;
            color: $primary_color;
        }
    }
    .components-base-control__field {
        .components-base-control__label {
            display: block;
            font-size: 13px !important;
            font-weight: 400 !important;
            line-height: 16px !important;
            margin: 5px 0 !important;
            min-height: auto;
            text-transform: capitalize !important;
        }
        .components-text-control__input, 
        .components-textarea-control__input {
            border: 1px solid $border_color;
            border-radius: 4px;
            box-shadow: none;
        }
        
        label {
            display: block;
            font-size: 13px !important;
            font-weight: 400 !important;
            line-height: 16px !important;
            margin: 5px 0 !important;
            min-height: auto;
            text-transform: capitalize !important;
        }
    }
}

.post-grid-block-setting-typography {
    .components-base-control__field {
        position: relative;
        display: flex;
        justify-content: space-between;
        margin: 0;
        align-items: center;
    }
}

.post-grid-block-setting-dropdown-icon-wrapper {
    .post-grid-block-setting-dropdown-icon-btn {
        margin: 0;
        height: 28px;
        width: 28px;
        padding: 5px;
        border: 1px solid #e6e7e9;
        border-radius: 14px;
        color: #50575e;
        background: $white_color;
        &:focus {
            outline: none;
            box-shadow: none;
        }
        .post-grid-block-setting-dropdown-icon {
            font-size: 15px;
            line-height: 20px;
        }
    }
}

.post-grid-block-setting-fontsize-wrapper {
    .post-grid-block-setting-fontsize-header {
        display: flex;
        align-items: center;
        justify-content: space-between;
        .post-grid-block-setting-fontsize-label {
            display: block;
            font-size: 13px !important;
            font-weight: 400 !important;
            line-height: 16px !important;
            margin: 5px 0 !important;
            min-height: auto;
            text-transform: capitalize !important;
        }
        .post-grid-block-setting-fontsize-units {

            .post-grid-block-setting-fontsize-units-wrap {
                direction: initial;
                display: flex;
                align-items: center;
                .post-grid-block-setting-fontsize-units-switcher {
                    box-shadow: unset;
                    font-size: 10px;
                    padding: 0;
                    text-transform: uppercase;
                    padding-right: 5px;
                    &.active {
                        color: $primary_color;
                    }
                }
            }
        }
    }
}

.post-grid-block-setting-letterspacing-wrapper {
        .post-grid-block-setting-letterspacing-header {
        display: flex;
        align-items: center;
        justify-content: space-between;
        .post-grid-block-setting-letterspacing-label {
            display: block;
            font-size: 13px !important;
            font-weight: 400 !important;
            line-height: 16px !important;
            margin: 5px 0 !important;
            min-height: auto;
            text-transform: capitalize !important;
        }
        .post-grid-block-setting-letterspacing-units {
            .post-grid-block-setting-letterspacing-units-wrap {
                direction: initial;
                display: flex;
                align-items: center;
                .post-grid-block-setting-letterspacing-units-switcher {
                    box-shadow: unset;
                    font-size: 10px;
                    padding: 0;
                    text-transform: uppercase;
                    padding-right: 5px;
                    &.active {
                        color: $primary_color;
                    }
                }
            }
        }
    }
}

.post-grid-block-setting-lineheight-wrapper {
    .post-grid-block-setting-lineheight-header {
        display: flex;
        align-items: center;
        justify-content: space-between;
        .post-grid-block-setting-lineheight-label {
            display: block;
            font-size: 13px !important;
            font-weight: 400 !important;
            line-height: 16px !important;
            margin: 5px 0 !important;
            min-height: auto;
            text-transform: capitalize !important;
        }
        .post-grid-block-setting-lineheight-units {
            .post-grid-block-setting-lineheight-units-wrap {
                direction: initial;
                display: flex;
                align-items: center;
                .post-grid-block-setting-lineheight-units-switcher {
                    box-shadow: unset;
                    font-size: 10px;
                    padding: 0;
                    text-transform: uppercase;
                    padding-right: 5px;
                    &.active {
                        color: $primary_color;
                    }
                }
            }
        }
    }
}

.post-grid-block-setting-typography-popup {
    padding: 0.2rem;
    min-width: 220px;
    .post-grid-block-setting-typography-popup-inner {
        position: relative;
        z-index: 99999;
        .components-base-control__field {
            display: flex;
            flex-direction: column;
            gap: 15px;
            label {
                display: block;
                font-size: 13px !important;
                font-weight: 400 !important;
                line-height: 16px !important;
                margin: 5px 0 !important;
                min-height: auto;
                text-transform: capitalize !important;
            }
        }
    }
}

.post-grid-block-setting-color-panel-wrapper {
    .post-grid-block-setting-color-panel-btn {
        display: flex !important;
        width: 100%;
        .post-grid-block-setting-color-panel-btn-switchr {
            width: 100%;
            justify-content: center;
            height: unset;
            padding: 10px;
            line-height: 16px;
            min-height: 30px;
            font-size: 13px;
            font-weight: 400;
            color: $secondary_color !important;
            box-shadow: inset 0 0 0 1px $background_color;
            &.active {
                background-color: $background_color;
            }
        }
    }
}

.geomap-block-setting-color-setting-wrapper {
    .components-base-control__field {
        position: relative;
        display: flex;
        justify-content: space-between;
        margin: 0;
        align-items: center;

        .post-grid-block-setting-color-setting-icon-dropdown {
            .post-grid-block-setting-color-setting-icon-btn {
                margin: 0;
                height: 28px;
                width: 28px;
                padding: 5px;
                border: 1px solid #e6e7e9;
                border-radius: 14px;
                color: #50575e;
                background: $white_color;
                &:focus {
                    outline: none;
                    box-shadow: none;
                }
                .post-grid-block-setting-color-setting-icon {
                    font-size: 15px;
                    line-height: 20px;
                }
            }
        }
    }
}

/** Global spacing style */
.post-grid-block-setting-spacing-container {
    display: flex;
    flex-direction: column;
    margin-bottom: 10px;
    .post-grid-block-setting-spacing-inner-container {
      margin-bottom: 20px;
      &:last-child {
        margin-bottom: 0;
      }
      .post-grid-block-setting-spacing-input-container {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
    
        .post-grid-block-setting-spacing-input-wrapper {
          display: flex;
          flex-direction: column;
          width: 20%;
    
          .post-grid-block-setting-spacing-input-label {
            text-align: center;
            font-size: 15px;
            margin-bottom: 5px;
          }
    
          &:first-child {
            input {
              border-bottom-left-radius: 5px;
              border-top-left-radius: 5px;
              border-width: 1px;
            }
          }
    
          input {
            border-color: #ebebeb !important;
            color: #1e1e1e;
            height: 30px !important;
            border: 1px solid #8d96a0;
            border-left-width: 0;
            border-radius: 0;
            height: 24px;
            margin: 0;
            min-height: 24px;
            padding: 0;
            text-align: center;
            width: 100%;
            font-size: 14px;
            &:focus {
              outline: none !important;
              box-shadow: none !important;
            }
          }
    
          span {
            border-color: #ebebeb !important;
            color: #1e1e1e;
            height: 30px !important;
            border: 1px solid #8d96a0;
            border-left-width: 0;
            border-radius: 0;
            height: 24px;
            margin: 0;
            min-height: 24px;
            padding: 0;
            text-align: center;
            width: 100%;
            margin-top: 18px;
            border-bottom-right-radius: 5px;
            border-top-right-radius: 5px;
            align-items: center;
            display: flex;
            justify-content: center;
            cursor: pointer;
            &.is-checked{
              background-color: #8d96a02b;
            }
          }
        }
      }
      .geomap-block-setting-spacing-label-container {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 10px;
      }
    }
}

.geomap-block-setting-label-wrapper {
    display: flex;
    align-items: center;
    p {
        margin-bottom: 0;
        margin-right: 5px;
        margin-top: 0;
        
        &.geomap-block-setting-label-text {
          flex: 1;
        }
    }
}

/** Global responsive device style */
.geomap-block-responsive-device-wrapper {
    display: flex;
    align-items: center;
    
    button {
      &.geomap-block-responsive-desktop-device, 
      &.geomap-block-responsive-tablet-device, 
      &.geomap-block-responsive-mobile-device {
        background: none;
        border: none;
        cursor: pointer;
        margin: 0;
        padding: 0;
        
        svg {
          fill: #cebeff;
          height: 10px;
          width: 15px;
        }
        
        &.active {
          color: #551ef7;
          font-weight: 600;
          
          svg {
            fill: #551ef7;
            height: 12px;
            width: 17px;
          }
        }
      }
    }
}

/** Global unit type style */
.geomap-block-setting-units-type {
    .geomap-block-setting-units-type-wrap {
        direction: initial;
        display: flex;
        align-items: center;
        .geomap-block-setting-units-type-switcher {
            box-shadow: unset;
            font-size: 10px;
            padding: 0;
            text-transform: uppercase;
            padding-right: 5px;
            &.active {
                color: $primary_color;
            }
        }
    }
}
/** Global alignment or select tag style */
.geomap-block-setting-text-alignment {
    .geomap-block-setting-text-alignment-group-btn {
        display: flex;
        .geomap-block-setting-text-alignment-single-btn {
            align-items: center;
            display: flex;
            flex: 1;
            justify-content: center;
            padding: 6px;
            height: unset;
            line-height: 16px;
            box-shadow: inset 0 0 0 1px $background_color;
            color: $secondary_color;

            &.active {
                background-color: $background_color !important;
                color: $secondary_color !important;
            }

            &:focus {
                outline: none;
                box-shadow: none;
            }

            &:hover {
                background-color: $background_color;
                color: $secondary_color;
            }
        }
    }
}
/** End Admin Panel scss **/

/** Start General scss for Advanced Button **/
.geomap-block-advanced-button-icon-popup {
    padding: 0.2rem;
    min-width: 220px;
    .geomap-block-advanced-button-icon-popup-inner {
        position: relative;
        z-index: 99999;
        .components-base-control__field {
            margin-bottom: 0;
            label {
                display: block;
                color: $secondary_color !important;
                font-size: 12px !important;
                font-weight: 500 !important;
                line-height: 16px !important;
                margin: 10px 0 !important;
                min-height: auto;
                text-transform: capitalize !important;
            }
            .components-toggle-control {
                margin-bottom: 0 !important;
            }
        }
    }
}

.geomap-block-link-control-wrapper {
    .geomap-block-link-control-inner-wrapper {
        display: flex;
        align-items: center;
        gap: 10px;
        .geomap-block-advanced-button-url {
            flex: 1;
            margin-bottom: 0;
            .components-base-control__field {
                margin-bottom: 0;
            }
        }
    }
}

.global-setting-spacing-container,
.geomap-block-default-range-control,
.geomap-block-setting-advanced-tab-wrapper,
.paneChild {
    .components-range-control__wrapper {
        span[aria-hidden="true"] {
            height: 2px !important;
        }
    }
    .components-range-control__thumb-wrapper {
        top: 0 !important;
        height: 8px !important;
        margin-top: 10px !important;
        width: 8px !important;
        span{
            &:before {
                height: 12px !important;
                left: -2px !important;
                top: -5px !important;
                width: 12px !important;
            }
        }
    }
    .components-range-control__tooltip {
        display: none !important;
    }

}

.geomap-block-tab-controlsgeneral,
.geomap-block-tab-controlsstyles,
.geomap-block-tab-controlsadvanced {
    .is-opened {
        .components-panel__body-title {
            margin: -16px -16px 0px;
        }
    }
    .components-base-control {
        margin-bottom: 15px;
    }
}




/** End General scss for Advanced Button **/